"use client";

import { useState } from "react";
import {
  Heart,
  ShoppingCart,
  CreditCard,
  Plus,
  Minus,
  RefreshCw,
  MessageSquare,
  Check,
} from "lucide-react";
import OfficeChair from "@/assets/images/office_seat.png";
import OfficeChair2 from "@/assets/images/office_seat_2.png";
import Image from "next/image";
import { AshHeartIcon } from "@/assets/svg/svg_icon";

const ProductDetailsComp = () => {
  const [selectedColor, setSelectedColor] = useState("black");
  const [quantity, setQuantity] = useState(1);
  const [selectedImage, setSelectedImage] = useState(0);
  const [isFavorite, setIsFavorite] = useState(false);

  const images = [OfficeChair, OfficeChair2, OfficeChair, OfficeChair2];

  const colors = [
    { name: "black", code: "#000000" },
    { name: "gray", code: "#D1D5DB" },
    { name: "brown", code: "#4A2511" },
  ];


const colorClasses: Record<string, string> = {
  black: "border-black",
  gray: "border-gray-300",
  brown: "border-[#4A2511]",
};


  const decreaseQuantity = () => {
    if (quantity > 1) setQuantity(quantity - 1);
  };

  const increaseQuantity = () => {
    setQuantity(quantity + 1);
  };

  return (
    <div className="p-6">
      <div className="container mx-auto">
        <div className="grid grid-cols-1 lg:grid-cols-[1fr_1.4fr_0.8fr] gap-5 h-full">
          {/* ==== Left Column - Image Gallery ====*/}
          <div className="lg:col-span-1 h-full">
            <div className="flex gap-2">
              {/* Thumbnail Column */}
              <div className="flex flex-col gap-3">
                {images.map((img, index) => (
                  <button
                    key={index}
                    onClick={() => setSelectedImage(index)}
                    className={`w-25 h-29 rounded-[18px] border-2 flex items-center justify-center text-3xl transition-all relative cursor-pointer ${
                      selectedImage === index
                        ? "border-[#fda106]"
                        : "border-gray-200 hover:border-gray-300"
                    }`}
                  >
                    <Image
                      src={img}
                      alt=""
                      fill
                      className="object-cover p-1 rounded-[18px]"
                    />
                  </button>
                ))}
              </div>

              {/* Main Image */}
              <div className="flex-1 rounded-[22px] flex border-2 border-gray-200 items-center justify-center relative w-full h-[500px]">
                <Image
                  src={images[selectedImage]}
                  alt="Selected"
                  fill
                  className="object-cover p-1 rounded-[22px]"
                />
              </div>
            </div>
          </div>

          {/*==== Middle Column - Product Details ====*/}
          <div className="lg:col-span-1 h-full">
            <div className="flex items-start justify-between mb-4">
              <h1 className="text-4xl font-bold text-gray-900">
                Ergonomic Office Chair
              </h1>
              <button
                onClick={() => setIsFavorite(!isFavorite)}
                className="p-2 hover:bg-gray-100 rounded-lg transition-colors"
              >
                <AshHeartIcon
                  className={`w-6 h-6 ${
                    isFavorite ? "fill-red-500 text-red-500" : ""
                  }`}
                />
              </button>
            </div>

            <p className="text-gray-600 mb-4 leading-relaxed text-lg">
              Lorem ipsum dolor sit amet consectetur. Orci ut elementum
              vulputate amet tellus. Egestas porta amet proin adipiscing
              interdum gravida non commodo sed.
            </p>

            {/* Rating */}
            {/* <div className="flex items-center gap-2 mb-4">
              <div className="flex text-yellow-400">
                {[...Array(5)].map((_, i) => (
                  <span key={i}>★</span>
                ))}
              </div>
              <span className="font-semibold text-gray-900">5.0</span>
              <span className="text-gray-600">(203 reviews)</span>
            </div> */}

            {/* Price */}
            <div className="text-4xl font-medium gradient-text mb-6">
              $350.00
            </div>

            {/* Color Selection */}
            <div className="mb-6">
              <h3 className="font-semibold text-gray-900 mb-3">Color:</h3>
              <div className="flex gap-4">
                {colors.map((color) => {
                  const isActive = selectedColor === color.name;

                  return (
                    <button
                      key={color.name}
                      onClick={() => setSelectedColor(color.name)}
                      className={`
                      w-10 h-10 rounded-full flex items-center justify-center
                      transition-all
                      ${isActive ? "scale-110" : ""}
                    `}
                      style={{
                        border: isActive
                          ? `2px solid ${color.code}`
                          : "2px solid transparent",
                      }}
                    >
                      {/* INNER COLOR DOT */}
                      <span
                        className="w-8 h-8 rounded-full block"
                        style={{ backgroundColor: color.code }}
                      />
                    </button>
                  );
                })}
              </div>
            </div>

            {/* Quantity */}
            <div className="mb-6">
              <h3 className="font-semibold text-gray-900 mb-3">Quantity</h3>
              <div className="flex items-center gap-3">
                <button
                  onClick={decreaseQuantity}
                  className="w-10 h-10 border-2 border-gray-300 rounded-lg flex items-center justify-center hover:bg-gray-50"
                >
                  <Minus className="w-4 h-4" />
                </button>
                <input
                  type="number"
                  value={quantity}
                  onChange={(e) =>
                    setQuantity(Math.max(1, parseInt(e.target.value) || 1))
                  }
                  className="w-16 h-10 text-center border-2 border-gray-300 rounded-lg focus:outline-none focus:border-emerald-900"
                />
                <button
                  onClick={increaseQuantity}
                  className="w-10 h-10 border-2 border-emerald-900 text-emerald-900 rounded-lg flex items-center justify-center hover:bg-emerald-50"
                >
                  <Plus className="w-4 h-4" />
                </button>
              </div>
            </div>

            {/* Action Buttons */}
            <div className="flex flex-wrap gap-3 mt-auto">
              <button className="flex-1 min-w-[140px] bg-orange-500 hover:bg-orange-600 text-white px-6 py-3 rounded-lg font-semibold flex items-center justify-center gap-2 transition-colors">
                <ShoppingCart className="w-5 h-5" />
                Add To Cart
              </button>
              <button className="flex-1 min-w-[140px] bg-emerald-900 hover:bg-emerald-800 text-white px-6 py-3 rounded-lg font-semibold flex items-center justify-center gap-2 transition-colors">
                <CreditCard className="w-5 h-5" />
                Buy Now
              </button>
              <button className="px-6 py-3 border-2 border-orange-500 text-orange-500 hover:bg-orange-50 rounded-lg font-semibold flex items-center gap-2 transition-colors">
                <Plus className="w-5 h-5" />
                Add Bundle
              </button>
            </div>
          </div>

          {/*==== Right Column - Seller Info ====*/}
          <div className="w-full h-full  max-w-md">
            <div className="rounded-[24px] border-2 border-dashed border-gray-300 p-6">
              <h2 className="text-xl font-bold text-gray-900 mb-6">Sold by</h2>

              {/* Seller Profile */}
              <div className="flex items-center gap-3 mb-4">
                <div className="w-12 h-12 bg-gray-200 rounded-full"></div>
                <div className="flex-1">
                  <div className="flex items-center gap-2">
                    <span className="font-semibold text-gray-900">
                      Offizzy Stores
                    </span>
                    <Check className="w-5 h-5 text-emerald-600 bg-emerald-100 rounded-full p-0.5" />
                  </div>
                  <div className="flex items-center gap-1">
                    <div className="flex text-yellow-400 text-sm">
                      {[...Array(5)].map((_, i) => (
                        <span key={i}>★</span>
                      ))}
                    </div>
                    <span className="text-sm font-semibold">4.3</span>
                    <span className="text-sm text-gray-600">(98 reviews)</span>
                  </div>
                </div>
              </div>

              {/* Stats */}
              <div className="grid grid-cols-2 gap-4 mb-6">
                <div>
                  <div className="text-2xl font-bold text-gray-900">90%</div>
                  <div className="text-sm text-gray-600">Seller ratings</div>
                </div>
                <div>
                  <div className="text-2xl font-bold text-gray-900">310+</div>
                  <div className="text-sm text-gray-600">Total products</div>
                </div>
              </div>

              <div className="mb-6">
                <div className="text-2xl font-bold text-gray-900">99%</div>
                <div className="text-sm text-gray-600">
                  Negotiation response
                </div>
              </div>

              {/* More Items Link */}
              <div className="text-center py-3 border-t border-gray-200 mb-4">
                <button className="text-gray-900 font-medium hover:text-emerald-900 transition-colors">
                  More Items From This Seller
                </button>
              </div>

              {/* Action Buttons */}
              <div className="space-y-3">
                <button className="w-full bg-emerald-50 hover:bg-emerald-100 text-emerald-900 px-6 py-3 rounded-lg font-semibold flex items-center justify-center gap-2 transition-colors border border-emerald-200">
                  <RefreshCw className="w-5 h-5" />
                  Negotiate Price
                </button>
                <button className="w-full bg-orange-50 hover:bg-orange-100 text-orange-500 px-6 py-3 rounded-lg font-semibold flex items-center justify-center gap-2 transition-colors border border-orange-200">
                  <MessageSquare className="w-5 h-5" />
                  Message Seller
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProductDetailsComp;
